'use client'

import { useParams } from 'next/navigation';
import { Breadcrumb } from '../../../components/SMBreadcrumb/SMBreadcrumb';
import { NavigableServicesMenu } from '../../../components/SMServices/SMNavigableServicesMenu';
import { ServicePage } from '../page';
import { ServicesContent } from '../../../components/SMServices/SMServicesContent';

export default function ServicesCatchAllPage() {
  const params = useParams();
  const slug = params.slug as string[] | undefined;

  // slug[0] = categorySlug, slug[1] = serviceId (optional)
  const categoryId = slug?.[0] || '';
  const serviceId = slug?.[1] || '';

  return (
    <div className="flex min-h-screen bg-gray-50">
      <NavigableServicesMenu />
      <div className="flex-1">
        {categoryId && serviceId ? (
          <ServicePage categoryId={categoryId} serviceId={serviceId} />
        ) : categoryId ? (
          // Если есть только категория - показываем контент с выбранной категорией
          <ServicesContent initialCategory={categoryId} />
        ) : (
          <ServicesContent />
        )}
      </div>
    </div>
  );
}
